import{
    Login,
    Home,
    NotFound
} from '../component/index.js'

export const routes = [
    {
        path: '/login',
        component: Login,
        exact: true
    },{
        path:'/',
        component: Home,
        exact: true
    },{
        path:'*',
        component: NotFound,
        exact: false
    }
]


